'use client'
import OrdersOverview from '@/components/content/ordersoverview';
import React from 'react';
import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';

const salesData = [
  { month: 'Jan', revenue: 4000, orders: 240 },
  { month: 'Feb', revenue: 3000, orders: 139 },
  { month: 'Mar', revenue: 5000, orders: 280 },
  { month: 'Apr', revenue: 4780, orders: 350 },
  { month: 'May', revenue: 5890, orders: 390 },
  { month: 'Jun', revenue: 4390, orders: 290 },
  { month: 'Jul', revenue: 4490, orders: 310 },
];

export default function SalesStatsPage() {
  return (
    <div className="p-6 space-y-10">
      {/*Header Section*/}
      <header/>
      <h1 className="text-3xl font-semibold text-gray-800 mb-6">Sales Statistics</h1>

      {/* KPI Section */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-10">
        <div className="bg-white p-6 shadow-lg rounded-lg hover:shadow-xl transition-shadow duration-300">
          <h2 className="text-lg font-medium text-gray-600">Total Revenue</h2>
          <p className="text-3xl font-semibold text-green-600">$25,000</p>
        </div>
        <div className="bg-white p-6 shadow-lg rounded-lg hover:shadow-xl transition-shadow duration-300">
          <h2 className="text-lg font-medium text-gray-600">Total Orders</h2>
          <p className="text-3xl font-semibold text-blue-600">1,200</p>
        </div>
        <div className="bg-white p-6 shadow-lg rounded-lg hover:shadow-xl transition-shadow duration-300">
          <h2 className="text-lg font-medium text-gray-600">Avg Order Value</h2>
          <p className="text-3xl font-semibold text-purple-600">$50</p>
        </div>
        <div className="bg-white p-6 shadow-lg rounded-lg hover:shadow-xl transition-shadow duration-300">
          <h2 className="text-lg font-medium text-gray-600">Returning Customers</h2>
          <p className="text-3xl font-semibold text-yellow-600">30%</p>
        </div>
      </div>

      {/* Sales Chart */}
      <div className="bg-white p-6 shadow-lg rounded-lg mb-10">
        <h2 className="text-xl font-semibold mb-4 text-gray-800">Revenue Trends</h2>
        <ResponsiveContainer width="100%" height={300}>
          <LineChart data={salesData} margin={{ top: 20, right: 30, left: 0, bottom: 5 }}>
            <CartesianGrid stroke="#f5f5f5" />
            <XAxis dataKey="month" />
            <YAxis />
            <Tooltip />
            <Line type="monotone" dataKey="revenue" stroke="#82ca9d" strokeWidth={2} />
            <Line type="monotone" dataKey="orders" stroke="#8884d8" strokeWidth={2} />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Recent Sales Table */}
      <div className="bg-white p-6 shadow-lg rounded-lg mb-10">
        <OrdersOverview />
      </div>
    </div>
  );
}
